/* Reset and base styles  */
@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&family=Roboto:wght@300;400;700&display=swap");
* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  width: 1230px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

/* Links */
a,
a:visited {
  text-decoration: none;
  display: inline-block;
}

a:hover {
  color: inherit;
  text-decoration: none;
}

a:focus,
a:active {
  outline: none;
}

/* Common */
aside,
nav,
footer,
header,
section,
main {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-size: inherit;
  font-weight: inherit;
}

ul {
  list-style: none;
  list-style-type: none;
}

img,
svg {
  display: block;
  width: 100%;
  height: auto;
}

/* Form */
input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
}

input::-ms-clear {
  display: none;
}

button,
input[type=submit] {
  display: inline-block;
  cursor: pointer;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

body {
  font-family: "Roboto Condensed", sans-serif;
}

html {
  scroll-behavior: smooth;
}

.header {
  height: 800px;
  position: relative;
  background-image: url(./../img/header/header-bg.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  margin-bottom: 75px;
}

.header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
}

.header__wrapper {
  border-bottom: 0.5px solid #878787;
  position: relative;
}

.header__content {
  text-align: center;
  position: relative;
  padding-top: 160px;
}

.header__title {
  font-weight: 300;
  font-size: 140px;
  line-height: 1.17;
  text-transform: uppercase;
  color: #fff;
}

.header__description {
  font-weight: 300;
  font-size: 20px;
  line-height: 1.35;
  color: #fff;
}

.header__scroll {
  position: absolute;
  bottom: 0;
  left: 50%;
  padding-bottom: 50px;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 45px;
  padding-bottom: 45px;
}

.nav__list {
  display: flex;
  gap: 50px;
}

.nav__link {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  text-transform: uppercase;
  color: #fff;
  transition: 0.2s;
}
.nav__link:hover {
  color: #fff;
}

.nav__tel a {
  position: relative;
}

.nav__tel a::before {
  content: "";
  background-image: url(../img/header/tel-white.svg);
  position: absolute;
  background-repeat: no-repeat;
  width: 23px;
  height: 25px;
  top: -6px;
  left: -31px;
}

.about {
  padding-top: 75px;
}

.about__title {
  margin-bottom: 50px;
}

.about__text {
  max-width: 520px;
  margin-bottom: 50px;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 18px;
  line-height: 1.5;
  color: #777777;
}

.about__wrapper {
  display: flex;
  justify-content: space-between;
}

.about-image img {
  width: 100%;
}

.portfolio {
  text-align: center;
  padding-top: 150px;
}

.portfolio__title {
  text-align: center;
  margin-bottom: 56px;
}

.request {
  padding-top: 130px;
  padding-bottom: 130px;
  text-align: center;
}

.request__link {
  position: relative;
  font-weight: 300;
  font-size: 40px;
  line-height: 1.175;
  text-transform: uppercase;
  color: #3D3D3D;
  transition: 0.3s;
  border-bottom: 1px solid #D5D5D5;
}
.request__link:hover {
  color: #3D3D3D;
}

.request__link::after {
  position: absolute;
  width: 15px;
  height: 15px;
  top: 8px;
  right: -21px;
  content: "";
  background-image: url(../img/request/arrow.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.footer {
  padding-top: 95px;
  padding-bottom: 95px;
  text-align: center;
  background: #F7F7F7;
}

.footer__socials a {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  text-transform: uppercase;
  color: #3D3D3D;
  transition: 0.3s;
}
.footer__socials a:hover {
  color: #3D3D3D;
}

.footer__socials {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-bottom: 30px;
}

.footer__copy {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 13px;
  line-height: 15px;
  color: #878787;
}

.logo {
  position: relative;
  width: 45px;
  height: 45px;
}

.accordion {
  max-width: 520px;
}

.accordion__title {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 27px;
  padding-bottom: 27px;
  border-top: 1px solid #D5D5D5;
  font-weight: 300;
  font-size: 20px;
  line-height: 1.35;
  text-transform: uppercase;
  color: #3D3D3D;
}

.accordion__title::after {
  content: "";
  background-image: url(../img/about/arrow.svg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 31px;
  height: 30px;
  transition: 0.2s ease-in-out;
}

.accordion__text p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #777777;
  margin-bottom: 27px;
}

.accordion__item:last-child {
  border-bottom: 1px solid #D5D5D5;
}

.accordion__title-active::after {
  content: "";
  background-image: url(../img/about/arrow.svg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 31px;
  height: 30px;
  transform: rotate(-92deg);
  transition: 0.2s ease-in-out;
}

.accordion__text {
  max-height: 0;
  overflow: hidden;
  transition: 1000ms ease max-height;
}

.title {
  font-weight: 300;
  font-size: 40px;
  line-height: 1.175;
  text-transform: uppercase;
  color: #3D3D3D;
}

.tabs__nav {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #D5D5D5;
  max-width: 662px;
  margin: 0 auto;
  margin-bottom: 40px;
}

.tabs__nav-item {
  cursor: pointer;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #878787;
  padding-bottom: 10px;
}

.tabs__nav-item.active {
  border-bottom: 2px solid #000000;
  color: #000;
}

.tabs__content {
  display: none;
}

.tabs__content.active {
  display: flex;
  justify-content: center;
}

.tabs__content-link {
  font-weight: 300;
  font-size: 20px;
  line-height: 1.35;
  color: #fff;
}

.tabs__content-image {
  position: relative;
  overflow: hidden;
}

.tabs__content-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #fff;
  opacity: 0;
  z-index: 3333;
  transition: 0.5s;
}

.tabs__content-text h3 {
  margin-bottom: 10px;
  text-transform: uppercase;
}

.tabs__content-image::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2;
  background: rgba(0, 0, 0, 0.6);
  transform: translateY(-100%);
  transition: 0.5s;
}

.tabs__content-link:hover .tabs__content-image::before {
  transform: translateY(0);
}

.tabs__content-link:hover .tabs__content-text {
  opacity: 1;
}

.tabs__content-container {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.tabs__content-right {
  display: flex;
  gap: 30px;
  justify-content: center;
  flex-wrap: wrap;
  margin-left: 30px;
}

.tabs__content-left {
  display: flex;
  gap: 30px;
  justify-content: center;
  flex-wrap: wrap;
}

.tabs__content-flex {
  display: flex;
  gap: 30px;
}

.tabs__content-left {
  display: flex;
  justify-content: flex-end;
}

.tabs__content-right {
  display: flex;
  justify-content: flex-start;
}

.tabs__content-left .tabs__content-link:nth-child(1) > .tabs__content-image {
  width: 277px;
}

.tabs__content-big {
  width: 585px;
}

.tabs__content-left .tabs__content-container > .tabs__content-link:nth-child(1) > .tabs__content-image {
  width: 277px;
}

.tabs__content-left .tabs__content-container > .tabs__content-link:nth-child(2) > .tabs__content-image {
  width: 277px;
}

.tabs__content-flex .tabs__content-link:nth-child(1) > .tabs__content-image {
  width: 277px;
}

.tabs__content-flex .tabs__content-link:nth-child(2) > .tabs__content-image {
  width: 277px;
}

.tabs__button {
  display: inline-block;
  cursor: pointer;
  margin-top: 40px;
  padding: 22px 54px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  color: #393246;
  text-transform: uppercase;
  border: 1px solid #393246;
  background: #fff;
}