/* Reset and base styles  */
* {
	padding: 0px;
	margin: 0px;
	border: none;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

:focus,
:active {
	// outline: none;
}

.container {
	width: 1230px;
	padding-left: 15px;
	padding-right: 15px;
	margin: 0 auto;

}

/* Links */

a,
a:visited {
	text-decoration: none;
	display: inline-block;
}

a:hover {
	color: inherit;
	text-decoration: none;
}

a:focus,
a:active {
	outline: none;
}

/* Common */

aside,
nav,
footer,
header,
section,
main {
	display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	font-size: inherit;
	font-weight: inherit;
}

ul {
	list-style: none;
	list-style-type: none;
}

img,
svg {
	display: block;
	width: 100%;
	height: auto;
}

/* Form */

input,
textarea,
button,
select {
	font-family: inherit;
	font-size: inherit;
}

input::-ms-clear {
	display: none;
}

button,
input[type="submit"] {
	display: inline-block;
	cursor: pointer;
}

input:focus,
input:active,
button:focus,
button:active {
	outline: none;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}

body {
	font-family: 'Roboto Condensed', sans-serif;
}

html {
	scroll-behavior: smooth;
}